.post__wrapper {
    margin: 40px 20px 20px;
}

.post__item, .post {
    padding: 20px;
    border-radius: 5px;
    margin: 10px;
    box-shadow: 0 1px 1px rgb(20 35 52 / 3%), 0 1px 3px rgb(20 35 52 / 4%), 0 2px 7px rgb(20 35 52 / 5%), 0 6px 24px rgb(20 35 52 / 7%);
}
.post__title{
    text-decoration: none;
    cursor: pointer;
    color: var(--navy);
}

.post__title:hover{
    color: var(--dark-green);
}

.posts-search-form{
    display: flex;
    align-items: baseline;
    margin: 20px 20px 20px 40px;
}

.posts-search-button{
    padding: 12px 20px 14px;
    border-radius: 5px;
    background-color: var(--dark-green);
    margin-left: 10px;
}

.post__body{
    margin-top: 10px;
}